  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height:100vh;
}
#root{
  height:100vh;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.map{
    width:100% !important; 
    height:91vh !important;
    margin-top: 5px;
}
.App{
    background-color: rgb(153, 153, 153);
}
.map-box{
display:flex;
}
